import { useEffect, useState, useRef } from 'react';
import loadingIcon from './img/loadingIcon.svg';
import arrowIcon from './img/arrowIcon.svg';

import './style.sass';
import { text } from './text.js'

function App() {
  const [page, setPage] = useState(0)
  const [wordIndex, setWordIndex] = useState(0)
  const [sentence, setSentence] = useState(null)
  const [pagesLoaded, setPagesLoaded] = useState(0)
  const [content, setContent] = useState([])
  const [mouseDown, setMouseDown] = useState(false)
  const [correctAnswer, setCorrectAnswer] = useState(false)
  const [firstLoad, setFirstLoad] = useState(null)
  const [translate, setTranslate] = useState(false)
  const [spans, setSpans] = useState([])
  const [phrase, setPhrase] = useState("")
  const [currentWord, setCurrentWord] = useState("")
  const [translation, setTranslation] = useState("")
  const [userAnswer, setUserAnser] = useState("")
  const [gameTranslation, setGameTranslation] = useState("...")
  const [pageInput, setPageInput] = useState(0)
  const [preStyle, setPreStyle] = useState({})
  const pRef = useRef(null);
  const lRef = useRef(null);
  const [scrolledDist, setScrolledDist] = useState(0)
  const [chapter, setChapter] = useState(0)
  const [touch, setTouch] = useState(false)
  const [endPage, setEndPage] = useState(null)
  const [loading, setLoading] = useState(false)
  const [game, setGame] = useState(false)
  const [canSwipe, setCanSwipe] = useState(true)
  const [scrollBehavior, setScrollBehavior] = useState("smooth")
  const [wordArray, setWordArray] = useState(text.replace(/(\r\n|\n|\r)/gm, " ").split(" "))
  const [favorites, setFavorites] = useState([])
  const [gameTasks, setGameTasks] = useState([])
  const [questionNumber, setQuestionNumber] = useState(null)
  const [gameButtonText, setGameButtonText] = useState("Next")


  // let a = 0
  // const [a, setA] = useState(true)
  // if (a) {
  //   console.log(pRef.current);
  //   setA(false)
  // }

  useEffect(() => {
    if (Object.keys(preStyle).length === 0) {

      let nextPreStyle = { ...preStyle }
      // if (pRef.current.offsetHeight / 25)
      // console.log(pRef.current.offsetHeight / Math.floor(pRef.current.offsetHeight / 25));
      let parentHeight = pRef.current.offsetHeight;
      nextPreStyle.lineHeight = parentHeight / Math.floor(parentHeight / 25) + "px"
      setPreStyle(nextPreStyle)
      // if (spans.includes(i)) {
      //   nextPreStyle.backgroundColor = "#ffffe0"
      // }
    }
  }, [preStyle])

  function handleMouse(event, i) {
    let nextSpans = [...spans]
    if (mouseDown) {
      // console.log(i);
      // check mobile or desktop
      // mobile
      if (touch) {
        // console.log(event.type);
        if (event.touches) {

          let spanNumber = parseInt(document.elementFromPoint(event.touches[0].clientX, event.touches[0].clientY).getAttribute("data-n"))
          if (!nextSpans.includes(spanNumber)) {
            nextSpans.push(spanNumber)

          } else {
            if (nextSpans.length > 1) {
              let firstElement = Math.min(...nextSpans)
              let lastElement = spanNumber > firstElement ? spanNumber : Math.max(...nextSpans)
              nextSpans = []
              for (let span = firstElement; span <= lastElement; span++) {
                nextSpans.push(span)
              }
            }
          }
          // console.log(nextSpans.map(el => content[el]).join(" "));

          // let req = 'https://translate-service.scratch.mit.edu/translate?language=ru&text=' + nextSpans.map(el => content[el]).join(" ")
          // console.log(req);

        }
      }
      // desktop
      else {
        if (!nextSpans.includes(i)) {
          nextSpans.push(i)

        } else {
          if (nextSpans.length > 1) {
            let firstElement = Math.min(...nextSpans)
            let lastElement = i > firstElement ? i : Math.max(...nextSpans)
            nextSpans = []
            for (let span = firstElement; span <= lastElement; span++) {
              nextSpans.push(span)
            }
          }
        }
      }
      setSpans(nextSpans)
      setPhrase(nextSpans.map(el => wordArray.slice(chapter, chapter + 1000)[el]).join(" "))
    }
  }

  useEffect(() => {
    setTranslation("...")
  }, [phrase])

  // useEffect(() => {
  //   console.log(spans);
  //   handleTranslate()
  // }, [spans])

  useEffect(() => {
    if (page !== undefined) {

      handleScroll()
    }
  }, [page])

  function handleTranslate(s) {
    let newPhrase = s ? s.replace(".", "") : phrase
    fetch("https://translate-service.scratch.mit.edu/translate?language=ru&text=" + newPhrase)
      .then(response => response.json())
      .then(commits => {
        setTranslation(commits.result)
      });
  }

  function handleScroll() {
    let scrollNumber = pRef.current.clientHeight

    let nextDist
    if (page === "down") {
      nextDist = 0
      setChapter(c => c + 1000)
      setPage(0)
    } else if (page == "up") {
      setChapter(c => c - 1000)
      setPage(Math.floor(pRef.current.scrollHeight / scrollNumber))
      console.log(Math.floor(pRef.current.scrollHeight / scrollNumber));
    }

    else {

      nextDist = scrollNumber * page
    }

    pRef.current.scrollTo({
      top: nextDist,
      left: 0,
      behavior: scrollBehavior,
    })

    setScrolledDist(nextDist)

    // }
  }

  // add new favorite word to favorites
  function handleFavorites() {
    let nextFavorites = [...favorites]
    let nextSentence = ""
    for (let s of document.getElementsByClassName(sentence)) {
      nextSentence = nextSentence + s.innerHTML + " "
    }
    nextFavorites.push({ sentence: nextSentence, word: currentWord })
    setFavorites(nextFavorites)
  }

  // translate task
  function translateQuestion() {
    fetch("https://translate-service.scratch.mit.edu/translate?language=ru&text=" + favorites[questionNumber].sentence.replace(/;/g, ""))
      .then(response => response.json())
      .then(commits => {
        setGameTranslation(commits.result)
      });
  }

  // check if user answer correct
  useEffect(() => {
    if (favorites.length > 0) {
      let intersection = userAnswer.split("").filter(x => !favorites[questionNumber].word.split("").includes(x)).concat(favorites[questionNumber].word.split("").filter(x => !userAnswer.split("").includes(x)));
      setCorrectAnswer(intersection.length < 3 ? true : false)
    }
  }, [userAnswer])


  function handleQuestion(event) {
    event.preventDefault()
    translateQuestion()
  }

  useEffect(() => {

    if (game) {
      setQuestionNumber(Math.floor(Math.random() * favorites.length))
      setGameButtonText("Next")
    }
  }, [game])

  useEffect(() => {
    console.log(gameTasks);
    if (game) {
      if (gameTasks.length == 0) {
        console.log("end");
        setGameButtonText("Play again")
      }
      else if (gameTasks.length != favorites.length) {
        setQuestionNumber(gameTasks[Math.floor(Math.random() * gameTasks.length)])
        setUserAnser("")
      } else {
        translateQuestion()
      }

    }
  }, [gameTasks])

  useEffect(() => {
    if (game) {
      if (gameTasks.length > 0 && gameTasks.length != favorites.length) {
        translateQuestion()
      } else {
        setGameTasks(favorites.map(el => favorites.indexOf(el)))
        console.log(favorites.map(el => favorites.indexOf(el)));
      }

    }
  }, [questionNumber, userAnswer])

  let sentenceNumber = 0;
  return (
    <div className="App">
      <div id="game" style={game ? { display: "block" } : { display: "none" }}>
        <form action="" onSubmit={(event) => {event.preventDefault(); setGameTasks(gt => { let nextGt = [...gt]; nextGt.splice(nextGt.indexOf(questionNumber), 1); return nextGt })}}>
          <p>{gameTranslation}</p>
          {favorites[questionNumber] ? <p>{favorites[questionNumber].sentence.split(favorites[questionNumber].word)[0]} <input type="text" value={userAnswer} onChange={(e) => setUserAnser(e.target.value)} /> {favorites[questionNumber].sentence.split(favorites[questionNumber].word)[1]}</p> : null}
          <button disabled={correctAnswer ? false : gameButtonText === "Next" ? true : false}>{gameButtonText}</button>
          {gameButtonText === "Play again" ? <button onClick={() => setGame(false)} type='button'>Close</button>:null}
        </form>
      </div>
      <h1>Kirill's</h1>
      <div className={"loadingModal " + (loading ? "" : "loadingClosed")}>
        {/* <img src={loadingIcon} alt="" /> */}
        <h3 ref={lRef}>Loading</h3>
      </div>
      <div id='mainText' onMouseDown={() => { setMouseDown(true); setSpans([]); }} onMouseUp={() => { setMouseDown(false); handleTranslate() }} onTouchStart={() => { setMouseDown(true); setSpans([]); setTouch(true) }} onTouchEnd={() => { setMouseDown(false); handleTranslate() }} ref={pRef}>
        {/* Lorem ipsum dolor sit amet consectetur adipisicing elit. Cumque ex, ipsa hic porro fuga veniam ea excepturi id, vel maiores aliquam. Nihil pariatur repellendus delectus quidem iusto temporibus, doloribus distinctio? */}
        {/* <Blank handleLoad={handleLoad}/> */}
        {
          wordArray.slice(chapter, chapter + 1000).map((el, i) => {
            if (wordArray.slice(chapter, chapter + 1000)[i - 1] && wordArray.slice(chapter, chapter + 1000)[i - 1].includes(".")) {
              sentenceNumber++
            }
            return <pre className={'sentence' + sentenceNumber} key={i} data-n={i} onDoubleClick={() => setTranslate(t => !t)} onClick={(event) => { setSpans([i]); handleTranslate(el); setSentence(event.target.className); setCurrentWord(el) }} onMouseMove={(event) => handleMouse(event, i)} onTouchMove={(event) => handleMouse(event, i)} style={spans.includes(i) ? { ...preStyle, backgroundColor: "#ffee0033" } : { ...preStyle }}>{el} </pre>
          }
          )
        }
      </div>
      <form action="" className="buttons" style={translate ? { opacity: 1, pointerEvents: "auto" } : {}}>
        <button id="hamburgerMenu" type='button'></button>
        <button type='button' onClick={() => setPage(p => p > 0 ? p - 1 : chapter != 0 ? "up" : 0)}></button>
        <h6>page<input type="text" value={chapter === "" ? "" : Math.floor(chapter / 1000)} onBlur={() => setChapter(chapter => chapter === "" ? 0 : chapter)} onChange={(e) => setChapter(e.target.value == "" ? "" : e.target.value * 1000 >= wordArray.length - 1000 ? wordArray.length - 1000 : e.target.value * 1000)} size={(chapter !== "" ? Math.floor(chapter / 1000).toString().length < 5 ? Math.floor(chapter / 1000).toString().length : 5 : 1)} /></h6>
        <button id="arrowDown" type='button' onClick={() => setPage(p => (pRef.current.scrollHeight <= scrolledDist + pRef.current.clientHeight) ? (wordArray.length - chapter) > 1000 ? "down" : p : p + 1)}></button>
        <button id="glitter" type='button' onClick={() => { setGame(true); }}></button>
      </form>
      <p>{translation}<button id="starButton" onClick={() => handleFavorites()}></button> </p>
    </div>
  );
}

export default App;
